@media screen and (max-width: 999px) {
  .logo-img {
    width: 90%;
    margin: 35px 0 10px; }
  .name-header {
    font-size: 8.5vw;
    padding-bottom: 10px; }
  .coins {
    text-align: center;
    height: auto;
    padding: 30px 0 50px; }
  .coins label {
    width: 33%;
    padding: 6px; }
  .coins-img {
    width: 100%;
    padding-top: 2px;
    position: relative; }
  .register {
    text-align: center;
    margin: 0;
    padding: 0; }
  .register-button, .register-mail, .register-social {
    float: none; }
  .register-social {
    margin-bottom: 30px; }
  .social-img {
    padding-right: 5.5%; }
  .register-mail {
    padding-left: 0px;
    text-align: center; }
  .register-button {
    padding: 0; }
  .mail-input-error-text {
    width: 100%;
    position: relative;
    margin: 10px 0; }
  .mail-input-error-text2 {
    width: 100%;
    position: relative; }
  #footer > .img2 {
    width: 16%; }
  #footer > span {
    font-size: 3vw;
    letter-spacing: 0; }
  .img1 {
    display: none; }
  .coins label {
    position: relative; }
  .coins-label-1, .coins-label-2 {
    margin: 0 6%; } }

@media screen and (max-width: 767px) {
  .mail-input {
    width: 90%; }
  .button-input,
  .button-input:hover {
    width: 90%;
    font-size: 10vw; }
  #footer {
    height: 80px; }
  #footer img {
    width: 10%;
    margin: 0 .5em; }
  #footer .support-img {
    margin-top: -.5em;
    width: 11%; } }
